'use client';

import { Wifi, WifiOff, Settings, AlertCircle, Plus } from 'lucide-react';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Table } from '@/components/ui/Table';
import { Chip } from '@heroui/chip';
import { Progress } from '@heroui/progress';
import { Dropdown, DropdownTrigger, DropdownMenu, DropdownItem } from '@heroui/dropdown';
import { useState } from 'react';

export default function ProvidersPage() {
    const [selectedKeys, setSelectedKeys] = useState(new Set<string>());

    const providers = [
        { id: '1', name: 'Main Gateway', status: 'online', uptime: 99.9, requests: 450000, location: 'US East', cpuUsage: 45, memoryUsage: 60 },
        { id: '2', name: 'Backup Gateway', status: 'online', uptime: 99.8, requests: 210000, location: 'US West', cpuUsage: 30, memoryUsage: 45 },
        { id: '3', name: 'Load Balancer', status: 'maintenance', uptime: 99.5, requests: 890000, location: 'EU Central', cpuUsage: 65, memoryUsage: 75 },
        { id: '4', name: 'SMS Gateway', status: 'offline', uptime: 95.2, requests: 0, location: 'Asia Pacific', cpuUsage: 0, memoryUsage: 0 },
    ];

    const getStatusColor = (status: string) => {
        switch (status) {
            case 'online': return 'success';
            case 'offline': return 'danger';
            case 'maintenance': return 'warning';
            default: return 'default';
        }
    };

    const columns = [
        {
            key: 'name',
            label: 'Provider',
            render: (item: any) => (
                <div className="flex items-center gap-3">
                    {item.status === 'online' ? (
                        <Wifi className="h-5 w-5 text-success" />
                    ) : item.status === 'offline' ? (
                        <WifiOff className="h-5 w-5 text-danger" />
                    ) : (
                        <Settings className="h-5 w-5 text-warning" />
                    )}
                    <div>
                        <p className="font-medium">{item.name}</p>
                        <p className="text-sm text-default-500">{item.location}</p>
                    </div>
                </div>
            ),
        },
        {
            key: 'status',
            label: 'Status',
            render: (item: any) => (
                <Chip
                    color={getStatusColor(item.status)}
                    variant="flat"
                    size="sm"
                >
                    {item.status.charAt(0).toUpperCase() + item.status.slice(1)}
                </Chip>
            ),
        },
        {
            key: 'uptime',
            label: 'Uptime',
            render: (item: any) => (
                <div className="space-y-1">
                    <Progress
                        value={item.uptime}
                        className="max-w-24"
                        color={item.uptime > 99 ? 'success' : item.uptime > 95 ? 'warning' : 'danger'}
                        size="sm"
                    />
                    <span className="text-sm font-medium">{item.uptime}%</span>
                </div>
            ),
        },
        {
            key: 'requests',
            label: 'Requests',
            render: (item: any) => (
                <span className="font-medium">{item.requests.toLocaleString()}</span>
            ),
        },
        {
            key: 'actions',
            label: 'Actions',
            render: (item: any) => (
                <Dropdown>
                    <DropdownTrigger>
                        <Button
                            isIconOnly
                            variant="light"
                            size="sm"
                        >
                            <Settings className="h-4 w-4" />
                        </Button>
                    </DropdownTrigger>
                    <DropdownMenu>
                        <DropdownItem key="edit">Edit Provider</DropdownItem>
                        <DropdownItem key="restart">Restart Service</DropdownItem>
                        <DropdownItem key="monitor">View Monitoring</DropdownItem>
                        <DropdownItem key="remove" color="danger">
                            Remove Provider
                        </DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            ),
        },
    ];

    return (
        <div className="space-y-6 animate-fade-in">
            <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-foreground">Providers</h1>
                    <p className="text-default-500 mt-2">Manage your USSD service providers and gateways</p>
                </div>
                <Button
                    color="primary"
                    startContent={<Plus className="h-4 w-4" />}
                >
                    Add Provider
                </Button>
            </div>

            {/* Provider Status Overview */}
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-6">
                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-success/10 rounded-xl">
                            <Wifi className="h-6 w-6 text-success" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Online Providers</p>
                            <p className="text-2xl font-bold mt-1">2</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-danger/10 rounded-xl">
                            <WifiOff className="h-6 w-6 text-danger" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Offline Providers</p>
                            <p className="text-2xl font-bold mt-1">1</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-primary/10 rounded-xl">
                            <AlertCircle className="h-6 w-6 text-primary" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-default-600">Total Requests</p>
                            <p className="text-2xl font-bold mt-1">1.55M</p>
                        </div>
                    </div>
                </Card>
            </div>

            {/* Providers Table */}
            <Card>
                <Table
                    data={providers}
                    columns={columns}
                    selectionMode="multiple"
                    selectedKeys={selectedKeys}
                    onSelectionChange={setSelectedKeys as any}
                    onRowAction={(key) => {
                        // Handle row click
                        console.log('Row clicked:', key);
                    }}
                />
            </Card>
        </div>
    );
}